import "../css/style.scss";
import {useState } from "react";

function Calculator() {
    const [calculation, setCalculation] = useState("");

    const [result, setResult] = useState("");

    const checkConsecutiveOperator = () => {
        const lastChar = calculation.substr(calculation.length - 1);


        if (lastChar === "+" || lastChar === "*" || lastChar === "/" || lastChar === "." || lastChar === "" && result.length < 1) {
            return true;
        } else {
            return false;
        }
    };

    const checkConsecutiveSubtract = () => {
        const lastChar = calculation.substr(calculation.length - 1);

        if (lastChar === "-") {
            return true;
        } else {
            return false;
        }
    }

    const checkConsecutiveZeroAtBeginning = (value) => {
        const lastChar = calculation.substr(calculation.length - 1);
    
        if (calculation == "0" && value == "0") {
            return true;
        } else {
            return false;
        }
    }

    const updateNum = (value) => {
        if(typeof result == "number") {
            setResult("")
        }

        // cek inputan awal tidak boleh 00
        if(checkConsecutiveZeroAtBeginning(value)) {
            return;
        }


        if(calculation.length == 1 && value == "0") {
            return;
        }

        setCalculation(calculation + value);
    };


    const updateOperator = (value) => {

        if (checkConsecutiveOperator()) {
            // return;
            const replaceOperator = calculation.substring(0,calculation.length - 1) + value;
            setCalculation(replaceOperator);
            return;
        }

        if (checkConsecutiveSubtract()) {
            const replaceOperator = calculation.substring(0,calculation.length - 1) + value;
            setCalculation(replaceOperator);
            return;
        }

        if (calculation == "" && typeof result == "number") {
            setCalculation(result + value);
            setResult("")
            // setCalculation(calculation + value);
            return;
        }

        setCalculation(calculation + value);
    };

    const updateSubtract = (value) => {
        if(checkConsecutiveSubtract()) {
            const replaceOperator = calculation.substring(0,calculation.length - 1) + "+";
            setCalculation(replaceOperator);
            return;
        }

        if (calculation == "" && typeof result == "number") {
            setCalculation(result + value);
            setResult("")
            // setCalculation(calculation + value);
            return;
        }

        setCalculation(calculation + value);
    }

    const equalHandler = () => {
        setResult(eval(calculation));
        console.log(result)
        console.log("result")

        setCalculation("")
    };

    const deleteHandler = () => {
        setCalculation(calculation.slice(0, -1));
    };

    const clearHandler = () => {
        setCalculation("");
        setResult("");
    };

    const decimalHandler = (value) => {
        if (checkConsecutiveOperator()) {
            return;
        }


        setCalculation(calculation + value);
    };

    return (
        <div id="container">
        
            <div className="text-box">
                <div id="result">{result}</div>
                <div id="display" style={{ textAlign: "right" }}>
                    {calculation}
                </div>
            </div>

            <div id="calculatorWrap">
                <div className="row mx-0">
                    <div id="seven" className="col button" value="7" onClick={() => updateNum("7")}>
                        7
                    </div>
                    <div id="eight" className="col button" value="8" onClick={() => updateNum("8")}>
                        8
                    </div>
                    <div id="nine" className="col button" value="9" onClick={() => updateNum("9")}>
                        9
                    </div>
                    <div id="del" className="col button" value="DEL" onClick={() => deleteHandler()}>
                        DEL
                    </div>
                </div>

                <div className="row mx-0">
                    <div id="four" className="col button" value="4" onClick={() => updateNum("4")}>
                        4
                    </div>
                    <div id="five" className="col button" value="5" onClick={() => updateNum("5")}>
                        5
                    </div>
                    <div id="six" className="col button" value="6" onClick={() => updateNum("6")}>
                        6
                    </div>
                    <div id="add" className="col button" value="+" onClick={() => updateOperator("+")}>
                        +
                    </div>
                </div>

                <div className="row mx-0">
                    <div id="one" className="col button" value="1" onClick={() => updateNum("1")}>
                        1
                    </div>
                    <div id="two" className="col button" value="2" onClick={() => updateNum("2")}>
                        2
                    </div>
                    <div id="three" className="col button" value="3" onClick={() => updateNum("3")}>
                        3
                    </div>
                    <div id="subtract" className="col button" value="-" onClick={() => updateSubtract("-")}>
                        -
                    </div>
                </div>

                <div className="row mx-0">
                    <div id="decimal" className="col button" value="." onClick={() => decimalHandler(".")}>
                        .
                    </div>
                    <div id="zero" className="col button" value="0" onClick={() => updateNum("0")}>
                        0
                    </div>
                    <div id="divide" className="col button" value="/" onClick={() => updateOperator("/")}>
                        /
                    </div>
                    <div id="multiply" className="col button" value="*" onClick={() => updateOperator("*")}>
                        *
                    </div>
                </div>

                <div className="row mx-0">
                    <div id="clear" className="col button" value="*clear" onClick={() => clearHandler()}>
                        A/C
                    </div>
                    <div id="equals" className="col button" value="=" onClick={() => equalHandler()}>
                        =
                    </div>
                </div>
            </div>
        </div>

    );
}

export default Calculator;
